//vue
import Vue from 'vue';
import Vuex from 'vuex';
import mutations from "./mutations";
import actions from "./actions";

//
Vue.use(Vuex);

//Store
const store = new Vuex.Store({
  //state
  //
  state: {
    topics: [
      {name: 'all', desc: '', selected: true},
      {name: 'good', desc: '', selected: false},
      {name: 'share', desc: '', selected: false},
      {name: 'ask', desc: '', selected: false},
      {name: 'job', desc: '', selected: false},
      {name: 'dev', desc: '', selected: false}
    ],
    //actionsheet
    //
    unLoginedMoreMenus: {
      Login: '',
      registered: ''
    },
    //
    loginedMoreMenus: {
      create:'',
      center: '',
      News: '',
      Logout: ''
    },
    //
    isLogined: false,
    //
    loginname:'',
    //accesstoken
    accesstoken:'',
    //id
    author_id:'',
    //actionSheet
    isShowMoreMenus: false,
  },
  //
  getters: {
    moreMenus: function (state) {
      if (state.isLogined) {
        //
        return state.loginedMoreMenus;
      }
      return state.unLoginedMoreMenus;
    },
    getTopicDescByTab:function (state) {
      return function (tab) {
        for(var i=0;i<state.topics.length;i++){
          if(state.topics[i].name==tab){
            return state.topics[i].desc
          }
        }
      }
    }
  },
//
  mutations,
  //
  actions
})

//
export default store;



